/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.mkdf-woo-single-page {
	
	&.mkdf-woo-single-thumb-on-left-side {
		
		.mkdf-single-product-content {
			
			.images {
				
				@media only screen and (min-width: $ipad-portrait-plus-pixel) {
					
					figure {
						width: 100%;
						margin: 0;
					}
					
					.woocommerce-product-gallery__image {
						
						&:first-child {
							left: 120px;
							width: calc(100% - 120px);
						}
					}
					
					&.woocommerce-product-gallery--with-images {
						
						.woocommerce-product-gallery__image {
							
							&:not(:first-child) {
								position: absolute;
								top: 0;
								left: 0;
								width: 100px;
								height: calc(25% + 5px);
								padding: 0 0 20px;
								box-sizing: border-box;
								
								a {
									height: 100%;
									
									img {
										height: 100%;
									}
								}
							}
							
							$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
							@for $i from 0 to length($thumbnail_numbers) {
								$thumbnail_number_value: nth($thumbnail_numbers, $i+1);
								
								&:nth-child(#{$thumbnail_number_value}#{n}) {
									$first_param: (25 * $i)#{'%'};
									$second_param: (5 * $i)#{px};
									$topPos: calc(#{$first_param} + #{$second_param});
									
									top: $topPos;
								}
							}
						}
					}
				}
			}
		}
	}
    
    .product {
	    @include mkdfRelativeHolderLayout();
	    float: none;
	    margin: 0 0 50px;
    }
        
    .mkdf-single-product-content {
        @include mkdfRelativeHolderLayout();
        clear: both;
        margin: 0 0 20px;
	
	    .images {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    width: 55%;
		    margin: 0;
		    padding: 0 29px 0 0;
		    float: left;
		    box-sizing: border-box;
		    
		    figure {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0 -10px;
			
			    a {
				    @include mkdfRelativeHolderLayout();
				
				    img {
					    position: relative;
					    width: 100%;
					    display: block;
				    }
			    }
		    }
		
		    .woocommerce-product-gallery__trigger {
				position: absolute;
			    top: 10px;
			    right: 39px;
			    width: 30px;
			    height: 30px;
			    line-height: 30px;
			    text-align: center;
			    z-index: 10;
		    }
		    
		    .woocommerce-product-gallery__image {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0;
			    padding: 0 10px;
			    box-sizing: border-box;
			    
			    &:first-child {
				    left: 10px;
				    width: calc(100% - 20px);
				    padding: 0;
			    }
			
			    .zoomImg {
				    cursor: crosshair;
			    }
		    }
		    
		    &.woocommerce-product-gallery--with-images {
			
			    .woocommerce-product-gallery__image {
				
				    &:not(:first-child) {
					    float: left;
					    padding-top: 20px;
				    }
			    }
		    }
		
		    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
		    @for $i from 0 to length($thumb_3_columns_label) {
			    &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
				    $column_width: 100%/($i+2);
				
				    .woocommerce-product-gallery__image {
					
					    &:not(:first-child) {
						    width: $column_width;
					    }
				    }
			    }
		    }
	    }
    }
	
    .mkdf-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 45%;
        padding: 0 0 0 29px;
        float: left;
        box-sizing: border-box;
	
	    .mkdf-single-product-title {
		    display: block;
		    margin: 0 0 14px;
	    }
	
	    .price {
		    @include mkdfWooCommercePriceStyle();
			font-size: 29px;
			ins{
				font-size: 22px;
				font-weight: 400;
			}
	    }
	
	    .woocommerce-product-rating {
		    @include mkdfRelativeHolderLayout();
		    line-height: 1;
		    margin: 22px 0 0;
			.star-rating{
				color: $first-main-color;
			}
	    }
	
	    .woocommerce-product-details__short-description {
		    @include mkdfRelativeHolderLayout();
		    margin: 25px 0 0;
		
		    p {
			    margin: 0;
		    }
	    }
	
	    .product_meta {
		    margin: 25px 0 0;
		
		    > span {
			    display: block;
			    margin: 0;
				color: $default-heading-color;
				font-family: $default-heading-font;
				font-size: 17px;
				letter-spacing: 0.075em;
				text-transform: uppercase;
				span, a{
					color: $default-text-color;
					font-family: $default-text-font;
					font-size: 16px;
					text-transform: capitalize;
					letter-spacing: 0;
				}
			    a {
				    color: $default-text-color;
				
				    &:hover {
					    color: $first-main-color;
				    }
			    }
		    }
	    }
	
	    .mkdf-woo-social-share-holder {
		    @include mkdfRelativeHolderLayout();
		
		    > * {
			    display: table-cell;
			    vertical-align: top;
		    }
		    > span {
			    padding: 0 9px 0 0;
				color: $default-heading-color;
				font-size: 14px;
				letter-spacing: 0.05em;
				text-transform: uppercase;
		    }
			li{
				margin: 0 10px 0 0;
			}
	    }
	
	    p.stock.out-of-stock,
	    p.stock.in-stock {
		    color: $default-heading-color;
		    font-weight: bold;
		    margin: 18px 0 0;
	    }
	
	    form.cart {
		    @include mkdfRelativeHolderLayout();
		    margin: 25px 0 0;
	    }
	
	    .mkdf-quantity-buttons {
		    margin: 0 15px 0 0;
			.mkdf-quantity-input{
				font-style: normal;
				font-size: 16px;
			}
			span{
				&.mkdf-quantity-plus,
				&.mkdf-quantity-minus{
					color: $default-heading-color;
					font-size: 15px;
				}
			}
	    }
    }
	
	.woocommerce-tabs {
		@include mkdfRelativeHolderLayout();
		margin: 38px 0 0;

		ul.tabs {
			@include mkdfRelativeHolderLayout();
			display: table;
			list-style: none;
			margin: 0;
			padding: 0;
			text-align: left;
			clear: both;
			
			> li {
				position: relative;
				display: inline-block;
				margin-right: 15px;
				&.active{
					a{
						&:after{
							width: 100%;
							background-color: #1b1b1b;
						}
					}
				}
				&:first-child{
					a{
						padding-left: 0;
					}
				}
				&:last-child{

				}
				a {
					position: relative;
					display: inline-block;
					vertical-align: top;
					padding: 5px 0;
					font-family: $default-heading-font;
					font-size: 17px;
					font-weight: 500;
					text-transform: uppercase;
					letter-spacing: 0.075em;
				&:after{
					content: '';
					position: absolute;
					bottom: 0;
					left: 0;
					width: 0;
					height: 1px;
					background-color: transparent;
					-webkit-transition: background-color .3s ease,width .25s ease;
					-moz-transition: background-color .3s ease,width .25s ease;
					transition: background-color .3s ease,width .25s ease;
				}
				}
			}
		}
		
		.entry-content {
			@include mkdfRelativeHolderLayout();
			
			> h2 {
				display: none;
			}
			
			> p {
				margin: 20px 20px 20px 0;
			}
		}
		
		table {
			margin: 17px 17px 17px 0;
			
			tr, th {
				border: 0;
			}
			
			td, th {
				padding: 3px 0;
				vertical-align: middle;
				text-align: left;
				
				p {
					margin: 0;
				}
			}
			
			th {
				color: $default-heading-color;
				font-family: $default-heading-font;
				font-size: 17px;
				letter-spacing: .075em;
				text-transform: uppercase;
				width: 1%;
				padding: 0 10px 0 0;
				font-weight: 400;
				&:after{
					content: ":";
				}
			}
			
			td {
				width: 99%;
			}
		}
		
		#reviews {
			@include mkdfRelativeHolderLayout();
			margin: 0 20px 0 0;
			
			h2 {
				margin: 24px 0;
				font-size: 23px;
				line-height: 1.565em;
			}
			
			ol.commentlist {
				list-style: none;
				
				li {
					margin: 0 0 15px;
					
					&:last-child {
						margin: 0;
					}
				}
				
				.comment_container {
					@include mkdfRelativeHolderLayout();
					
					img {
						float: left;
						width: 70px;
					}
				}
				
				.comment-text {
					padding: 0 0 0 90px;
					
					p {
						margin: 0;
					}
				}
			}
			.woocommerce-noreviews{
				margin: 0 0 5px;
			}
			
			.comment-respond {
				.comment-reply-title {
					display: inline-block;
					margin: 0 0 5px;
				}
				.stars {
					overflow: hidden;
					margin: 2px 0 13px;
					line-height: 0;
					a {
						position: relative;
						display: inline-block;
						color: transparent;
						font-size: 0;
						margin: 0;
						@include mkdfWooCommerceSingleProductRatingsStyle();
						&.active{
							&:after{
								bottom: 0px;
							}
						}
					}
				}
				.comment-notes{
					margin: 0 0 5px;
				}
			}
		}
	}
	
	.related.products,
	.upsells.products {
		margin: 30px 0 0;
		
		> h2 {
			display: block;
			margin: 36px 0 30px;
			font-size: 23px;
			text-align: left;
		}
		
		.product {
			
			.mkdf-pl-text-wrapper {
				margin-bottom: 0;
			}
			.star-rating{
				color: $first-main-color;
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */