/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.mkdf-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    padding: 0 10px;

    .touch & {
        cursor: pointer;
    }

    .mkdf-shopping-cart-inner {
        position: relative;
        display: table;
        height: 100%;
    }

    .mkdf-header-cart {
        position: relative;
        display: table-cell;
        vertical-align: middle;

        .mkdf-cart-icon {
            position: relative;
            display: inline-block;
            vertical-align: top;
        }

        &.mkdf-header-cart-icon-pack {
            .mkdf-cart-icon {
                font-size: 19px;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
            .mkdf-cart-number{
                position: absolute;
                top: 28px;
                right: -8px;
                background-color: $first-main-color;
                width: 15px;
                height: 15px;
                line-height: 15px;
                border-radius: 50%;
                text-align: center;
                font-size: 10px;
                color: #fff;
                font-weight: 600;

                .mkdf-light-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.mkdf-fixed-wrapper) &{
                    background-color: $first-background-color;
                    color: $first-main-color;
                }
            }
        }

        &.mkdf-header-cart-svg-path {
            .mkdf-cart-icon {

                >* {
                    vertical-align: middle;
                }
                
                > *, 
                path {
                    fill: currentColor;
                    stroke: currentColor;
                }
            }
        }
    }

    &:hover {

        .mkdf-shopping-cart-dropdown {
            visibility: visible;
            max-height: 100vh;
            opacity: 1;
            @include mkdfTransition(max-height 0.6s cubic-bezier(.53,.01,.18,1), opacity 0s ease);
        }
    }
}

.mkdf-dark-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.fixed) .mkdf-shopping-cart-holder .mkdf-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.mkdf-light-header .mkdf-page-header > div:not(.mkdf-sticky-header):not(.fixed) .mkdf-shopping-cart-holder .mkdf-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }
}

.mkdf-shopping-cart-dropdown {
    position: absolute;
    top: 100%;
    left: auto;
    right: 0;
    width: 280px;
    background-color: #fff;
    text-align: left;
    z-index: 1000;
    max-height: 0;
    opacity: 0;
    visibility: hidden;
    overflow: hidden;
    box-sizing: border-box;
    @include mkdfTransition(opacity 0.2s, max-height 0s linear 0.15s);


    ul {
        margin: 25px 38px 28px;
        padding: 0;

        li {
            @include mkdfTableLayout();
            padding: 0 0 28px;
            box-sizing: border-box;

            &.mkdf-empty-cart {
                color: $first-main-color;
                font-size: 15px;
                border-bottom: 0;
                padding: 0;
            }

            &:first-child {
                padding-top: 0;
            }

        }
        .mkdf-dropdown-product-list{
            margin-top: 37px;
            li{
                &:last-child{
                    padding: 0 0 12px;
                }
            }
        }
    }

    .mkdf-item-image-holder {
        position: relative;
        display: table-cell;
        height: 100%;
        width: 55px;
        vertical-align: middle;

        img {
            vertical-align: middle;
            max-height: 55px;
        }
    }

    .mkdf-item-info-holder {
        @include mkdfTableCellLayout();
        padding: 5px 14px 0px;

        .mkdf-product-title {
            display: block;
            margin: 0;
            box-sizing: border-box;
            font-size: 15px;
        }

        .remove {
            position: absolute;
            top: -3px;
            right: 0;
	        font-size: 12px;
	        line-height: 1;

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .mkdf-quantity {
            display: inline-block;
            margin: 0;
            font-family: $default-text-font;
            font-size: 15px;
            color: $default-heading-color;
            letter-spacing: 0.05em;
        }
        .mkdf-dropdown-price{
            >span{
                display: inline-block;
                font-family: $default-text-font;
                font-size: 15px;
                color: $default-heading-color;
            }
        }

        .amount {
            margin: 0 0 0 5px;

        }
    }

    .mkdf-cart-bottom {
        padding: 0;
        margin: 0 0 37px;

        .mkdf-subtotal-holder {
            position: relative;
            display: table;
            width: 100%;
            margin: 0 0 15px;

            > * {
                display: inline-block;
                font-family: $default-text-font;
                color: $default-heading-color;
                font-size: 15px;
            }
            .mkdf-total-amount{
                margin-left: 5px;
            }
        }

        .mkdf-view-cart {
            @include mkdfRelativeHolderLayout();
            height: 50px;
            line-height: 50px;
            padding: 0 26px;
            font-size: 12px;
            font-weight: 700;
	        letter-spacing: 2px;
            color: $first-main-color;
            background-color: #FFF;
            border: 1px solid $first-main-color;
            text-align: center;
            box-sizing: border-box;
            @include mkdfTransition(color .2s ease-out, background-color .2s ease-out);

            span {
                display: block;
            }

            &:hover {
                color: #fff;
                background-color: $default-heading-color;
            }
        }
    }
}

.mkdf-vertical-menu-area {

    .mkdf-shopping-cart-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */